<template>
  <div class="c">
    <div class="demo-text">
      <h3>codeMirror</h3>
      <p> mybatis Redis prometheussqlsqlinputcodeMirror
      </p>
      <p>vue-codemirrorjssqlFormatter</p>
      <p>tipsF2Ctrl
      </p>
    </div>
    <codemirror v-model="item"
      :options="cmOption"
      class="code-mirror"
      ref="codeMirror"
      @ready="onCmReady3"
      @input="onCmCodeChange"
      @keyHandled="keyHandled"></codemirror>
  </div>
  <!-- @focus="onCmFocus" -->
</template>

<script>
// import codemirror from 'vue-codemirror'
import 'codemirror/lib/codemirror.css'
// JS
import 'codemirror/mode/javascript/javascript.js'
// 
import sqlFormatter from 'sql-formatter'
// import  from 'sql-formatter'

import 'codemirror/addon/hint/sql-hint.js'
import 'codemirror/mode/sql/sql.js'
import './setting.js'
// console.log(sqlFormatter, format)
export default {

  props: ['context'],
  watch: {
    context (newVal) {
      this.item = newVal
    }
  },
  data () {
    return {
      item: this.context,
      cmOption: {
        tabSize: 2, // tab
        mode: 'sql', // ,  codemirror/mode 
        lineNumbers: true, // 
        styleActiveLine: true, // 
        styleSelectedText: true,
        matchBrackets: true, // 
        line: true,
        foldGutter: true, // 
        gutters: ['CodeMirror-linenumbers', 'CodeMirror-foldgutter'],
        highlightSelectionMatches: { showToken: /\w/, annotateScrollbar: true }, // 
        // hint.js options
        hintOptions: {
          // 
          completeSingle: true
        },
        //   sublimeemacsvim
        keyMap: 'sublime',
        showCursorWhenSelecting: true,
        theme: 'monokai', // 
        extraKeys: { Ctrl: 'autocomplete', F2: 'format' }// keyMap
      }
    }
  },
  methods: {
    /*  */
    format () {
      /*  */
      console.log(sqlFormatter)
      let sqlContent = ''
      sqlContent = this.$refs.codeMirror.codemirror.getValue()
      /* sql */
      this.$refs.codeMirror.codemirror.setValue(sqlFormatter.format(sqlContent))
    },
    onCmReady3 () {
      this.$refs.codeMirror.codemirror.setSize('100%', '600')
    },
    keyHandled (cm, name, e) {
      if (name === 'Cmd-Enter') {
        this.format()
      }
    },
    onCmCodeChange (newCode) {
      this.$emit('outSQL', newCode)
    }
  }
}
</script>
